import React, { ReactElement } from 'react';


export type IPriority = 1 | 2 | 3 | 4 | 5;

export const Priority = {

  min: () => 1 as IPriority,

  max: () => 5 as IPriority,

  isMin: (p: IPriority): boolean => p === Priority.min(),

  isMax: (p: IPriority): boolean => p === Priority.max(),

  next: (p: IPriority): IPriority => Priority.isMax(p) ? p : (p + 1) as IPriority,

  prev: (p: IPriority): IPriority => Priority.isMin(p) ? p : (p - 1) as IPriority,

}

type ISlotWidth = number | { staticMeasure: () => React.ReactElement };

type ISlotFixedProps = {

  key: string;

  mode: 'fixed';

  priority: IPriority;

  align: 'left' | 'right';

  width: ISlotWidth;

  children: React.ReactElement;
};


type ISlotDiscreteProps = {

  key: string;

  mode: 'discrete';

  priority: Array<IPriority>;

  align: 'left' | 'right';

  width: Array<ISlotWidth>;

  children: Array<React.ReactElement>;
};


type ISlotDynamicProps = {

  mode: 'dynamic';

  priority: IPriority;

  align: 'left' | 'right';

  width: 'free' | 'controlled';

  children: (width?: number) => React.ReactElement;
};

export type ILocationBarSlotProps = ISlotFixedProps | ISlotDiscreteProps | ISlotDynamicProps;

export const LocationBarSlot: React.FC<ILocationBarSlotProps> = () => null;
